import {
    getAuth,
    createUserWithEmailAndPassword,
    signInWithEmailAndPassword,
    signOut,
    updateProfile
} from "firebase/auth";

const initialState = {
    inAuth: false,
    name: ''
}

export const authReducer = (state = initialState, action) => {
    switch (action.type) {
        case 'IN-AUTH' : {
            return {...state, inAuth: action.inAuth}
        }
        case 'SET_NAME' : {
            return {...state, name: action.name}
        }
        default:
            return state;
    }
}

//ACTION CREATER
export const changeInAuth = (inAuth) => {
    return {
        type: 'IN-AUTH', inAuth
    }
}
export const setName = (name) => {
    return {
        type: 'SET_NAME', name
    }
}

//THUNK
export const createUserTC = (email, password, name) => async (dispatch) => {
    const auth = getAuth();

    try {
        const userCredential = await createUserWithEmailAndPassword(auth, email, password)
        const userName = await updateProfile(auth.currentUser, {
            displayName: name
        })
        dispatch(changeInAuth(true))
        dispatch(setName(userCredential.user.displayName))
        const user = userCredential.user;
    } catch (error) {
        const errorCode = error.code;
        const errorMessage = error.message;
    }

}

export const signInTC = (email, password) => async (dispatch) => {
    const auth = getAuth();

    try {
        debugger
        const userCredential = await signInWithEmailAndPassword(auth, email, password)
        dispatch(changeInAuth(true))
        const user = userCredential.user;
        dispatch(setName(userCredential.user.displayName))
    } catch (error) {
        const errorCode = error.code;
        const errorMessage = error.message;
    }


}

export const signOutTC = () => async (dispatch) => {
    const auth = getAuth();

    try {
        const singOut = await signOut(auth)
        dispatch(changeInAuth(false))
    } catch (error) {

    }
}
